import React,{Component} from 'react';
import './App.css';
import './tachyons.css';
import Product from './components/Products'
import mango from './images/mango.jpg'
import apple from './images/apple.jpg'

import orange from './images/orange.jpg'

import banana from './images/banana.jpg'

import watermelon from './images/watermelon.jpg'

import grapes from './images/grapes.jpg'
import papaya from './images/papaya.jpg'
const products=[
{
  id:1,
  name:"Mango",
  description:"very very very delicious mangoes from MP",
  img:mango,
  price:100
},
{
  id:2,
  name:"Apple",
  description:"very very very delicious Aples from MP",
  img:apple,
  price:100
}
,
{
  id:3,
  name:"Oranges",
  description:"very very very delicious Oranges from MP",
  img:orange,
  price:100
}
,
{
  id:4,
  name:"Bananas",
  description:"very very very delicious Aples from MP",
  img:banana,
  price:100
}
,
{
  id:5,
  name:"WaterMelon",
  description:"very very very delicious WaterMelon from MP",
  img:watermelon,
  price:100
},
{
  id:6,
  name:"Grapes",
  description:"very very very delicious WaterMelon from MP",
  img:grapes,
  price:100
},
{
  id:7,
  name:"Papaya",
  description:"very very very delicious WaterMelon from MP",
  img:papaya,
  price:100
}
]
//pa3 pa5 tacynos wrapepr
class App extends Component {
  constructor(){
    super();
    this.state={
      cart:[
        {
          id:1,
          name:"Mango",
          description:"very very very delicious mangoes from MP",
          img:"/images/mango.jpg",
          price:100,
          units:1
        }
      ]
    };
  }
  handleaddCartFunc(product){
    var index=this.state.cart.findIndex(p=>p.id===product.id);
      if(index!==-1){
        this.state.cart[index].units=this.state.cart[index].units+1;
        this.setState({
          cart:this.state.cart
        });
      }
      else{
          this.setState({
            cart:[...this.state.cart,product]
          });
      }
  }
  handleRemoveCartFunc(productId){
    var index=this.state.cart.findIndex(p=>p.id===productId);
    if(index!==-1){
      if(this.state.cart[index].units>1){
        this.state.cart[index].units=this.state.cart[index].units-1;
        this.setState({
          cart:this.state.cart
        });
      }
      else{
        const filteredPeople = this.state.cart.filter((item) => item.id !== productId);
        this.setState({
          cart:filteredPeople
        });
      }
      
    }
    
  }
  handleCheckout(){

    console.log('Check out Data');
  }
  render(){
    return (
      <div className="App">
       <article className="center mw5 mw6-ns br3 hidden ba b--black-10 mv4">
          <h1 className="f4 bg-near-white br3 br--top black-60 mv0 pv2 ph3">Shopping Cart</h1>
        <div className="pa3 bt b--black-10">
          <ul>
        { this.state.cart.map(c=><li> <u>Product Name:</u> {c.name} | Units: {c.units}  <button className="f6 link dim br3 ph3 pv2 mb2 dib white bg-light-red bn"onClick={()=>this.handleRemoveCartFunc(c.id)}>Remove</button></li>)}

          </ul>

          <button className="f6 ml3 pr2 link dim br3 ph3 pv2 mb2 dib white bg-purple bn" onClick={()=>this.handleCheckout()}>CheckOut</button>
        </div>
      </article>
      <h1 align="center"><b>Products</b></h1>
        <div className="pa3 pa5-ns flex flex-wrap">
      {
        products.map(p=><Product key={p.id}{...p} addCartFunc={this.handleaddCartFunc.bind(this)}/>)
      }
        </div>
      </div>
    );
  }
}

export default App;
